.outlet{
    display: flex;
    justify-content: center;
}


/*transitions between routes properties*/
.appear {
    position: absolute;
}

.enter {
    opacity: 0;
    transform: scale(1.1);
}

.enterActive {
    opacity: 1;
    transform: scale(1);
    transition: opacity 300ms, transform 300ms;
}

.exit {
    opacity: 1;
    transform: scale(1);
}

.exitActive {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
}